import React from "react";
import One from './components/One'
import Two from './components/Two'
import Three from './components/Three'
import Four from './components/Four'
import Five from './components/Five'
import Left from './components/Left'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hide: false,
      detail: null
    };
    this.showDetails = this.showDetails.bind(this)
  }

  showDetails(detail = null) {


    this.setState({ hide: !this.state.hide, detail });

  }
  renderHidden() {
    const { detail } = this.state
    switch (detail) {
      case 'one':
        return <One />
      case 'two':
        return <Two />
      case 'three':
        return <Three />
      case 'four':
        return <Four />
      case 'five':
        return <Five />
      default:
        return null
    }
  }

  render() {
    const { hide, detail } = this.state;

    return (
      <div class="wrap">
        <h1 className="absolute-label">Omega</h1>
        <div class={hide ? "hide left-side" : "left-side"}>
          <Left/>
        </div>
        <div class="right-side">
          <section className={hide && detail === 'one' ? "fixed" : null} id="one">
            section 1
            {hide ? <button

              onClick={() => {
                this.showDetails();
              }}
            >
              back to all
            </button> :
              <button
                onClick={() => {
                  this.showDetails("one");
                }}
              >
                show one details
            </button>}
          </section>
          <section className={hide && detail === 'two' ? "fixed" : null} id="two">
            section 2
            {hide ? <button

              onClick={() => {
                this.showDetails();
              }}
            >
              back to all
            </button> :
              <button
                onClick={() => {
                  this.showDetails("two");
                }}
              >
                show two details
            </button>}
          </section>
          <section className={hide && detail === 'three' ? "fixed" : null} id="three">
            section 3
            {hide ? <button

              onClick={() => {
                this.showDetails();
              }}
            >
              back to all
            </button> :
              <button
                onClick={() => {
                  this.showDetails("three");
                }}
              >
                show three details
            </button>}
          </section>
          <section
            className={hide && detail === 'four' ? "fixed" : null}
            id="four"
          >
            section 4
            {hide ? <button

              onClick={() => {
                this.showDetails();
              }}
            >
              back to all
            </button> :
              <button
                onClick={() => {
                  this.showDetails("four");
                }}
              >
                show four details
            </button>}
          </section>
          <section
            className={hide && detail === 'five' ? "fixed" : null}
            id='five'
          >
            section 5
            {hide ? <button

              onClick={() => {
                this.showDetails();
              }}
            >
              back to all
            </button> :
              <button
                onClick={() => {
                  this.showDetails("five");
                }}
                id="five"
              >
                show five details
            </button>}
          </section>
          <section className="locked"></section>
        </div>
        <section
          class={!hide ? "hide hidden-section" : "hidden-section"}>
          {this.renderHidden()}
        </section>

      </div>
    );
  }
}

export default App;
